.movie-listing {
  padding: 20px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.title {
  font-size: 24px;
  font-weight: bold;
}

.search {
  display: flex;
  align-items: center;
}

.search input {
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 10px;
}

.search-icon {
  cursor: pointer;
  font-size: 20px;
}

.movie-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 20px;
}

.movie {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.poster {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.title {
  margin-top: 10px;
  text-align: center;
  font-size: 16px;
}
